"use client";
import { getAppointments } from "@/app/(entities)/appointment/api/getAppointments";
import AppointmentCard from "@/app/_components/AppointmentCard";
import { useQuery } from "@tanstack/react-query";

export default function AppointmentsList() {
  const { data: appointments } = useQuery({
    queryKey: ["appointments"],
    queryFn: () => getAppointments({ pageParam: null }),
  });

  return (
    <div>
      {appointments?.map((item) => (
        <AppointmentCard key={item.id} card={item} />
      ))}
    </div>
  );
}
